.card {
  margin: 10px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 6px 18px -9px rgba(0, 0, 0, 0.75);
  transition: transform 100ms ease-in;
  cursor: pointer;
  width: 25%;
  flex-direction: row;
  display: inline-block;
  justify-content: center;
  flex-wrap: wrap;
  flex-grow: 1;
  color: black;
}


.card:hover {
  transform: scale(1.07);
}

.card > img {
  object-fit: fill;
  min-width: 250px;
  min-height: 300px;
  width: 30%;
  height: 30%;
  flex-direction: column;
  display: block;
  justify-content: center;
  flex-wrap: wrap;
}

.card__info {
    margin-top: -9px;
    border-radius: 10px;
    padding: 20px;
    padding-top: 20px;
    border: 1;
    
  }

  .card__info > h2 {
    font-size: 18px;
    font-weight: 600;
    
  }
  
  .card__info > h4 {
    font-size: 14px;
    font-weight: 300;
    margin-top: 8px;
    margin-bottom: 8px;
    
  }

    
@media (max-width: 800px) {
  .card, .card > img {
    max-width: 375px;
    flex-wrap: wrap;
    flex-direction: row;
  }
}